import React, { Fragment } from "react";
import { AccessControlTypes, Template } from "types";
import { Button } from "antd";
import {
  ModalContent,
  ModalOpenButton,
  useModalContext,
} from "components/ui/Modal";
import { Form, FormContentBase } from "components/ui/Form/Form";

export default function TemplateModal() {
  const { onOk, form } = useModalContext();

  function handleOk() {
    onOk("/template/export", "create", {} as Template);
  }

  return (
    <Fragment>
      <ModalOpenButton requiredRoles={["Administrator"]} requiredAccessControls={AccessControlTypes.Create} allowedWithOneWayGitSync={true}>
        <Button children="Export Configuration as Template" />
      </ModalOpenButton>
      <ModalContent title="Export Configuration as Template" onClickOk={handleOk}>
        <Form schemaName="template">
          <FormContentBase
            name="create_template_form"
            form={form}
            preserve={false}
          />
        </Form>
      </ModalContent>
    </Fragment>
  );
}
