import React from 'react';
import { Upload, message, Button, Modal, Descriptions, Typography, Divider, Input, Space } from 'antd';
import { UploadOutlined } from '@ant-design/icons';
import { Template, Variable } from 'types';
import axios from "axios";

export default function ImportTemplateButton(props) {
  const isDownload = props.download;
  const [template, setTemplate] = React.useState<Template>(null);
  const [modalOpen, setModalOpen] = React.useState(false);
  const [loading, setLoading] = React.useState(false);
  const [downloading, setDownloading] = React.useState(false);
  const [fields, setFields] = React.useState<Array<Variable>>([]);

  const downloadTemplate = async () => {
    setDownloading(true);
    var response = await axios.post<Template>(`/api/v1/template/read/${props.name}/${props.version}`);

    if (response.status !== 200) {
      message.error(response.statusText);
      setDownloading(false);
      return
    }

    setTemplate(response.data);
    setDownloading(false);
    setModalOpen(true);
  }

  const updateField = (name, value) => {
    fields.find(m => m.name === name).value = value;
    setFields(fields);
  }

  const updateUserName = (name, username) => {
    fields.find(m => m.name === name).userName = username;
    setFields(fields);
  }

  const updatePassword = (name, password) => {
    fields.find(m => m.name === name).password = password;
    setFields(fields);
  }

  const uploadProps = {
    name: 'file',
    action: '/api/v1/template/read',
    onChange(info) {
      if (info.file.status === 'done') {
        setModalOpen(true);
        setTemplate(info.file.response);
        setFields(info.file.response.variables);
      } else if (info.file.status === 'error') {
        message.error(`${info.file.name} file upload failed.`);
      }
    },
  };

  const onCancel = () => {
    setLoading(false);
    setTemplate(null);
    setModalOpen(false);
  }

  const onOk = () => {
    setLoading(true);
    axios.post("/api/v1/template/import", fields).then(x => {
      message.success("Template applied successfully!")
      setLoading(false);
      setTemplate(null);
      setModalOpen(false);
    }).catch(() => {
      message.error("Template failed to apply!");
      setLoading(false);
      setTemplate(null);
      setModalOpen(false);
    })
  }

  const createVariableField = (variable: Variable) => {
    var input = null;
    if (variable.vault && variable.vault !== '') {
      if (variable.type === "String") {
        input = <Input.Password placeholder={variable.name} onChange={e => updateField(variable.name, e.target.value)} />
      }
      else if (variable.type === "PSCredential") {
        input = (
          <React.Fragment>
            <Input placeholder="User Name" onChange={e => updateUserName(variable.name, e.target.value)} />
            <Input.Password placeholder="Password" onChange={e => updatePassword(variable.name, e.target.value)} />
          </React.Fragment>
        )
      }
    }
    else {
      input = <Input placeholder={variable.name} onChange={e => updateField(variable.name, e.target.value)} />
    }
    return <React.Fragment>
      <Typography>${variable.name}</Typography>
      <Typography.Text type="secondary">{variable.description}</Typography.Text>
      {input}
    </React.Fragment>
  }

  return (
    <React.Fragment>
      {!isDownload && <Upload {...uploadProps}>
        <Button icon={<UploadOutlined />}>Import Template</Button>
      </Upload>}
      {isDownload && <Button icon={<UploadOutlined />} loading={downloading} onClick={downloadTemplate} >Import Template</Button>}
      <Modal visible={modalOpen} onOk={onOk} onCancel={onCancel} footer={
        [
          <Button type="default" onClick={onCancel}>
            Cancel
          </Button>,
          <Button key="submit" type="primary" loading={loading} onClick={onOk}>
            Apply
          </Button>
        ]
      }>
        <Descriptions title={template?.name}>
          <Descriptions.Item label="Version">{template?.version}</Descriptions.Item>
          <Descriptions.Item label="Author">{template?.author}</Descriptions.Item>
        </Descriptions>
        <Typography.Text type="secondary">{template?.description}</Typography.Text>
        {template?.variables.length > 0 && <>
          <Divider />
          <Typography.Title level={4}>Variables</Typography.Title>
          <Typography.Text type="secondary">Customize variables for this template.</Typography.Text>
          <p></p>
          <Space direction="vertical">
            {
              template?.variables.map(createVariableField)
            }
          </Space>
        </>}

      </Modal>
    </React.Fragment>
  )
}