import React from "react";
import { CustomColumns, Template } from "../../types";
import { useQuery } from "react-query3";
import { PageHeader } from "antd";
import { Modal } from "components/ui/Modal";
import Table from "components/ui/Table";
import TemplateModal from "./templateModal";
import ImportTemplateButton from "./importTemplateButton";
import DocsButton from "components/docsButton";

const columns: CustomColumns<Template>[] = [
    {
        title: "Name",
        dataIndex: "name",
        key: "name",
        editable: false
    },
    {
        title: "Description",
        dataIndex: "description",
        key: "description",
        editable: false,
    },
    {
        title: "Version",
        dataIndex: "version",
        key: "version",
        editable: false,
    },
    {
        width: "fit-content",
        dataIndex: "actions",
        editable: false,
        render: (text, record) => {
            return (
                <ImportTemplateButton name={record.name} version={record.version} download={true} />
            );
        },
    },
];

export default function Templates() {
    const { data: templates, isLoading, refetch } = useQuery<Template[]>(
        "/template",
        {
            enabled: false,
            refetchOnWindowFocus: false,
        }
    );

    return (
        <PageHeader title="Templates" subTitle="Community contributed templates that contain pre-built configurations." extra={[
            <DocsButton url="platform/templates" />
        ]}>
            <Table
                title="Templates"
                actions={[
                    <Modal>
                        <TemplateModal />
                    </Modal>,
                    <ImportTemplateButton />
                ]}
                columns={columns}
                data={templates}
                loading={isLoading}
                queryFn={refetch}
            />
        </PageHeader>

    );
}
